import Project from './Project'

function Projects({projects}) {

 
    return (
        <div id='projects'>
                <h1 className='section__heading' style={{margin : '0 5%'}}>Projects</h1>
                <p style={{margin : '0 5%' , fontSize : '13px' , color : 'gray'}}>*Checkout my <a href="https://github.com/soheilcode" style={{color : '#4DA1A9'}}>Github Repositories</a> for more...</p>
                <div className="projects">
                    {
                        projects?.map((project , index) => (
                                    <Project name={project.name} icon={project.icon} screenShot={project.screenShot} key={index} id={index} desc={project.desc} techs={project.tech}/>
                        ))
                    }

                </div>
        </div>



    )
}

export default Projects
