import { useEffect, useState } from "react"
import { useHistory, useParams } from "react-router-dom"
import Techs from "./Techs"
import { icons } from "./Icons"



function ProjectPage({projects}) {
    const history = useHistory()
    const [project , setProject] = useState(null)
    const {index} = useParams()
    useEffect(() => {
    if(projects && index) {
        setProject(projects[index])
    }else {
        history.push('/')
    }
    } , [index , projects , history])

    useEffect(() => {
        window.scrollTo(0, 0)
    },[])
    
    return (
        <div className="projectPage">
            <div className="project__top">
                   <img src={project?.icon} alt="" className="project__icon projectPage__icon"/>
               
                <div className="project__info">
                    <h3 style={{color: '#b2b2b2'}} className="project__des">{project?.desc}</h3>
                    <h1 className="project__name">{project?.name}</h1>
                </div>
                <div className="projectPage__btns">
                    {
                        project?.liveLink ? (
                            <>
                                      <a href={project?.liveLink} className=" btn__live">LIVE</a>
                                      <a href={project?.codeLink} className=" btn__code">CODE</a>
                            </>
                        ) : (
                            <a href={project?.codeLink} className="btn__only">CODE</a>
                        )
                    }
              
                </div>
              </div>
              <div className="page__shots">
                {
                    project?.shots.map((shot , index) => (
                        
                        <img src={shot} alt={index} key={index} className={index%2 !== 0 ? "page__phoneShot" : "page__desktopShot"}/>
                    ))
                }
              </div>
                <div className="project__icons">
                            {
                                project?.tech.map(tech => (
                                    <Techs icon={icons[tech]} size={50}/>
                                ))
                            }
                    </div>
              <div className="page__description">
                <p>{project?.description}</p>

              </div>
        </div>
    )
}

export default ProjectPage
