import { Link } from "react-router-dom"

import {icons} from './Icons'
import Techs from "./Techs"
function Project({icon , name , screenShot, id , desc , techs}) {
    return (

        
        <div className="project">
            <div className="project__wrap" >
                
              <div className="project__top">
                  
                   <img src={icon} alt="" className="project__icon"/>
               
                <div className="project__info">
                
                    <h3 style={{color: '#b2b2b2'}} className="project__des">{desc}</h3>
                    <h1 className="project__name">{name}</h1>

                </div>
                <div style={{display : 'flex' , flexDirection : 'column' , alignItems : 'flex-end'}}>
                    <Link to={`project/${id}`} className="project__button" >See</Link>
                    <div className="project__icons">
                            {
                                techs.map((tech , index) => (
                                    <Techs icon={icons[tech]} key={index} size={30}/>
                                ))
                            }
                    </div>

                </div>
              </div>
            <img src={screenShot} alt="" className="project__shot"/>
            </div>
        </div>

    )
}

export default Project
